<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="queryForm">
				<el-form-item>
					<el-input v-model="filterText" placeholder=""></el-input>
				</el-form-item>
        <el-form-item>
					<el-button @click="handleDetail" :disabled="this.checkedNodeData===null" v-hasPermission="'system.auth.detail'"></el-button>
				</el-form-item>
        <el-form-item>
					<el-button type="primary" @click="handleAdd" :disabled="this.checkedNodeData===null" v-hasPermission="'system.auth.add'"></el-button>
				</el-form-item>
        <el-form-item>
					<el-button type="warning" @click="handleEdit" :disabled="this.checkedNodeData===null" v-hasPermission="'system.auth.update'"></el-button>
				</el-form-item>
        <el-form-item>
					<el-button type="danger" @click="handleDel" :disabled="this.checkedNodeData===null" v-hasPermission="'system.auth.delete'"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
    <el-col :span="24">
      <el-tree
        class="filter-tree"
        :data="treeData"
        :props="defaultProps"
        node-key="id"
        @node-click="checkNode"
        auto-expand-parent
        :filter-node-method="filterNode"
        :default-expanded-keys="[-1]"
        accordion
        ref="tree">
      </el-tree>
    </el-col>

		<!---->
		<el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="authName">
					<el-input v-model="editForm.authName" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="" prop="authCode">
					<el-input v-model="editForm.authCode" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="" prop="authType">
          <el-radio-group v-model="editForm.authType">
            <el-radio label="menu"></el-radio>
            <el-radio label="oper"></el-radio>
          </el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="authName">
					<el-input v-model="addForm.authName" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="" prop="authCode">
					<el-input v-model="addForm.authCode" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="" prop="authType">
          <el-radio-group v-model="addForm.authType">
            <el-radio label="menu"></el-radio>
            <el-radio label="oper"></el-radio>
          </el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>

    <!---->
		<el-dialog title="" :visible.sync="detailFormVisible" :close-on-click-modal="false">
			<el-form :model="detailForm" label-width="80px" ref="detailForm">
				<el-form-item label="">
          <span>{{detailForm.authName}}</span>
				</el-form-item>
        <el-form-item label="">
          <span>{{detailForm.authCode}}</span>
				</el-form-item>
        <el-form-item label="">
          <span>{{detailForm.authTypeName}}</span>
				</el-form-item>
        <el-form-item label="">
          <span>{{detailForm.level}}</span>
				</el-form-item>
        <el-form-item label="">
          <span>{{detailForm.createTime}}</span>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="detailFormVisible = false"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
export default {
  data() {
    return {
      filterText: '',
      queryForm: {
        authName: ""
      },
      treeData: [],
      checkedNodeData: null,
      defaultProps: {label: 'authName', children: 'subAuthList'},

      editFormVisible: false, //
      editLoading: false,
      editFormRules: {
        authName: [
          { required: true, message: "", trigger: "blur" }
        ],
        authCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        authType: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //
      editForm: {
        id: 0,
        authName: '',
        authCode: '',
        authType: '',
        parentId: null
      },

      addFormVisible: false, //
      addLoading: false,
      addFormRules: {
        authName: [
          { required: true, message: "", trigger: "blur" }
        ],
        authCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        authType: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //
      addForm: {
        authName: '',
        authCode: '',
        authType: '',
        parentId: null
      },

      detailFormVisible: false, //
      detailForm: {}
    };
  },
  methods: {
    filterNode(value, data) {
      if (!value) return true;
      if (data.id === -1) return true;
      return data.authName.indexOf(value) !== -1;
    },
    checkNode(data,node){
      this.checkedNodeData = data;
    },
    //
    getTreeData() {
      var _this = this;
      _this.$ajax.get("/system/auth/tree").then(res => {
        _this.treeData = [];
        if (res.success) {
          _this.treeData.push(res.root);
        } else {
          _this.treeData.push({id: -1, authName: '', subAuthList: []});
        }
      });
    },
    //
    handleDel: function() {
      var _this = this;
      if(_this.checkedNodeData==null){
        _this.$message.info("")
        return
      }
      if(_this.checkedNodeData.id === -1){
        _this.$message.info("")
        return
      }
      _this.$confirm(""+_this.checkedNodeData.authName+"", "", {}).then(function(){
        _this.$ajax.post('/system/auth/delete/'+_this.checkedNodeData.id).then(function(res){
          if(res.success){
            _this.$message.success('')
            _this.checkedNodeData = null
            _this.getTreeData()
          }
        })
      })
    },
    //
    handleEdit: function() {
      var _this = this;
      if(_this.checkedNodeData==null){
        _this.$message.info("")
        return
      }
      if(_this.checkedNodeData.id === -1){
        _this.$message.info("")
        return
      }
      _this.$ajax.get('/system/auth/get/'+_this.checkedNodeData.id).then(function(res){
        if(res.success){
          _this.editForm = res.data
          _this.checkedNodeData = null
          _this.editFormVisible = true
        }
      })
    },
    //
    handleAdd: function() {
      var _this = this;
      if(_this.checkedNodeData==null){
        _this.$message.info("")
        return
      }
      _this.addForm = {
        authName: '',
        authCode: '',
        authType: '',
        parentId: null
      }
      if(_this.checkedNodeData.id !== -1){
        _this.addForm.parentId = _this.checkedNodeData.id
      }
      _this.checkedNodeData = null
      _this.addFormVisible = true
    },
    //
    handleDetail:function(){
      var _this = this;
      if(_this.checkedNodeData==null){
        _this.$message.info("")
        return
      }
      if(_this.checkedNodeData.id === -1){
        _this.$message.info("")
        return
      }
      _this.$ajax.get('/system/auth/get/'+_this.checkedNodeData.id).then(function(res){
        if(res.success){
          _this.detailForm = res.data
          _this.checkedNodeData = null
          _this.detailFormVisible = true
        }
      })
    },
    //
    editSubmit: function() {
      var _this = this;
      _this.$refs.editForm.validate(valid => {
        if (valid) {
          _this.$confirm("", "", {}).then(() => {
            _this.editLoading = true;
            let para = Object.assign({}, _this.editForm);
            _this.$ajax.post("/system/auth/update", para).then(res => {
              _this.editLoading = false;
              if (res.success) {
                _this.$message({
                  message: "",
                  type: "success"
                });
                _this.$refs["editForm"].resetFields();
                _this.editFormVisible = false;
                _this.getTreeData();
              }
            });
          });
        }
      });
    },
    //
    addSubmit: function() {
      var _this = this;
      _this.$refs.addForm.validate(valid => {
        if (valid) {
          _this.$confirm("", "", {}).then(() => {
            _this.addLoading = true;
            let para = Object.assign({}, _this.addForm);
            _this.$ajax.post("/system/auth/add", para).then(res => {
              _this.addLoading = false;
              if (res.success) {
                _this.$message({
                  message: "",
                  type: "success"
                });
                _this.$refs["addForm"].resetFields();
                _this.addFormVisible = false;
                _this.getTreeData();
              }
            });
          });
        }
      });
    }
  },
  mounted(){
    this.getTreeData()
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  }
};
</script>

<style scoped lang="scss">

</style>



