<template>
  <section>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="queryForm">
        <el-form-item>
				  <el-input v-model="queryForm.propertyDesc" placeholder=""></el-input>
				</el-form-item>
        <el-form-item>
					<el-input v-model="queryForm.propertyName" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getTableData"></el-button>
				</el-form-item>
        <el-form-item>
					<el-button type="primary" @click="handleAdd" v-hasPermission="'system.config.add'"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="tableData" stripe highlight-current-row v-loading="listLoading" style="width: 100%;" :empty-text="message">
			<el-table-column type="index" width="60" label="">
			</el-table-column>
      <el-table-column prop="propertyName" label="" width="250" show-overflow-tooltip>
			</el-table-column>
      <el-table-column prop="propertyValue" label="" width="300" show-overflow-tooltip>
			</el-table-column>
      <el-table-column prop="propertyDesc" label="" show-overflow-tooltip>
			</el-table-column>
      <el-table-column label="">
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)" v-hasPermission="'system.config.edit'"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)" v-hasPermission="'system.config.delete'"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="total, prev, pager, next" @current-change="handleCurrentChange" :page-size="pageSize" :total="total" prev-text="" next-text="" background style="float:right;">
			</el-pagination>
		</el-col>

    <!---->
		<el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="propertyName">
					<el-input v-model="editForm.propertyName" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="" prop="propertyValue">
					<el-input v-model="editForm.propertyValue" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="" prop="propertyDesc">
					<el-input v-model="editForm.propertyDesc" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="propertyName">
					<el-input v-model="addForm.propertyName" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="" prop="propertyValue">
					<el-input v-model="addForm.propertyValue" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="" prop="propertyDesc">
					<el-input v-model="addForm.propertyDesc" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
  </section>
</template>

<script>
export default {
    data(){
      return {
        queryForm: {
          propertyDesc: '',
          propertyName: ''
        },
        tableData: [],
        total: 0,
        pages: 0,
        page: 1,
        pageSize: 8,
        message: '',
        listLoading: false,
				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					propertyName: [
						{ required: true, message: '', trigger: 'blur' }
          ],
          propertyValue: [
            { required: true, message: '', trigger: 'blur' }
          ]
				},
				//
				editForm: {
          id: '',
					propertyName: '',
					propertyValue: '',
					propertyDesc: ''
				},
				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					propertyName: [
						{ required: true, message: '', trigger: 'blur' }
          ],
          propertyValue: [
            { required: true, message: '', trigger: 'blur' }
          ]
				},
				//
				addForm: {
					propertyName: '',
					propertyValue: '',
					propertyDesc: ''
				}
      }
    },
    methods: {
      handleCurrentChange(val) {
				this.page = val;
				this.getTableData();
      },
      //
      getTableData() {
				let para = {
          propertyDesc: this.queryForm.propertyDesc,
          propertyName: this.queryForm.propertyName,
          page: this.page,
          pageSize: this.pageSize,
				};
        this.listLoading = true;
				this.$ajax.get('/system/config/page',para).then(res => {
          this.listLoading = false;
          if(res.success){
            this.page = res.page;
            this.pageSize = res.pageSize;
            this.total = res.total;
            this.pages = res.pages;
            this.tableData = res.data;
          }else{
            this.tableData = [];
            this.page = 0;
            this.total = 0;
            this.pages = 0;
            this.message = res.message;
          }
				});
      },
      //
      handleAdd: function () {
        this.addForm = {
					propertyName: '',
					propertyValue: '',
					propertyDesc: ''
        };
				this.addFormVisible = true;
      },
      //
      handleEdit: function (index, row) {
        this.editForm = Object.assign({}, row);
				this.editFormVisible = true;
      },
      //
      editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							let para = Object.assign({}, this.editForm);
							this.$ajax.post('/system/config/update',para).then((res) => {
                this.editLoading = false;
                if(res.success){
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                  this.$refs['editForm'].resetFields();
                  this.editFormVisible = false;
                  this.getTableData();
                }
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							let para = Object.assign({}, this.addForm);
							this.$ajax.post('/system/config/add',para).then((res) => {
								this.addLoading = false;
								if(res.success){
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                  this.$refs['addForm'].resetFields();
                  this.addFormVisible = false;
                  this.getTableData();
                }
							});
						});
					}
				});
      },
      handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					this.$ajax.post('/system/config/delete/'+row.id).then((res) => {
            this.listLoading = false;
            if(res.success){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getTableData();
            }
					});
				}).catch(() => {

				});
			}
    },
    created(){
      this.getTableData()
    }
}
</script>




